<template>
<div class="col-12">
    <div class="d-flex justify-content-center"><h2>Upcoming-Movies</h2></div>
    
     <swiper
    :effect="'coverflow'"
    :grabCursor="true"
    :centeredSlides="true"
    :slidesPerView="'auto'"
    :coverflowEffect="{
      rotate: 50,
      stretch: 0,
      depth: 100,
      modifier: 1,
      slideShadows: true,
    }"
    :pagination="true"
    :modules="modules"
    class="mySwiper"
  >
    <swiper-slide 
    v-for="upcomes in upcome"
    :key="upcomes"
      ><router-link :to="`/movie/${upcomes.id}`"><img
        v-bind:src="'https://image.tmdb.org/t/p/w500/' + upcomes.poster_path" /></router-link></swiper-slide>
  </swiper>
</div>

 
</template>
<script>
// Import Swiper Vue.js components
import { Swiper, SwiperSlide } from "swiper/vue";

// Import Swiper styles
import "swiper/css";

import "swiper/css/effect-coverflow";
import "swiper/css/pagination";
import api from "@/api/api";
import "../assets/css/s.css";

// import required modules
import { EffectCoverflow, Pagination } from "swiper";

export default {
    name: "swap-er",
    data() {
        return {
            upcome: []
        }
    },
  components: {
    Swiper,
    SwiperSlide,
  },
  setup() {
    return {
      modules: [EffectCoverflow, Pagination],
    };
  },
  mounted() {
      this.gg()
  },
  methods: {
      gg() {
          api.get(`/movie/upcoming`)
    .then(gg => {
      this.upcome = gg.data.results.slice(1,9)
      console.log(this.upcome.fff)
    })
    .catch(e => {
      console.log(e)
    })
      }
 
  }
};
</script>