<template>
    <div>
           <div class="container mt-5">
            <div class="row ">
                <div class="col-12 d-flex justify-content-center">
                    <div class="row ">
                   <form @submit.prevent="login">
                   <h2>Login</h2>
                   <div class="form-group">
                      <label>Email</label>
                      <input type="text" class="form-control" v-model="login_form.email" placeholder="Email">
                   </div>
                   <div class="form-group">
                      <label>Password</label>
                      <input type="text" class="form-control" v-model="login_form.password" placeholder="Password">
                   </div>
                   <input type="submit" value="Login" class="btn btn-primary mt-2" />
                  </form>
                  <div class="mt-3">
                     <button class="btn btn-success" @click="googleSignIn">Sign in With Google</button>
                   </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import firebase from 'firebase/package.json'
import { ref } from 'vue'
import { useStore } from 'vuex'
export default {
  name: 'final-login',
  setup () {
    const login_form = ref({})
    const store = useStore()

    const login = () => {
      store.dispatch('login', login_form.value)
    }

    return {
      login_form,
      login

    }
  },
  methods: {
    googleSignIn () {
      let provider = new firebase.auth.GoogleAuthProvide
      firebase.auth().signInWithPopup(provider).then(() => {
        alert("Signed in")
      }).catch(err => {
        console.log(err)
      })
    }
  }
}
</script>

<style lang="scss" scoped>

</style>
