<template>
  <div class="home">
   <div class="container">
     <div class="row">
       <div class="col-12">

           <PopumovTitle />

       </div>
     </div>
   </div>
   <div class="container mt-4">
     <div class="row d-flex">
       <div class="col-12">
         <div class="row">
           <div class="col-12">
              <div class="card-columns">
                <Popumov :key="list.id" v-for="list in lists" :list="list"/>
              </div>
           </div>
         </div>

       </div>
     </div>
   </div>
   <div class="row mt-4">
     <div class="col-12">
        <Swap />
     </div>
   </div>
   <div class="container">
     <div class="row">
       <div class="col-12">
           <UpcomingMovie />
       </div>
     </div>
   </div>
  </div>
</template>

<script>
// @ is an alias to /src
import Popumov from '../components/Popularmovies.vue'
import PopumovTitle from '../components/Popularmoviestitle.vue'
import UpcomingMovie from '@/components/UpcomingMovie.vue'
import api from "../api/api"
import Swap from "../components/swap.vue"

export default {
  name: 'HomeView',
  components: {
    Popumov, PopumovTitle,UpcomingMovie,Swap
  },
  data () {
    return {
      lists: [],
    }
  },
  async mounted () {
    await api.get(`/discover/movie?sort_by=popularity.desc`)
    .then(response => {
      this.lists = response.data.results.slice(1,11)
      console.log(this.lists)
    })
    .catch(e => {
      console.log(e)
    });
  },
}
</script>
