<script>
import api from '@/api/api'
import medis from '../components/media.vue'

export default {
  name: "movie-b",
  components: {
     medis,
  },
  data () {
    return {
      movie: [],
      modelOpen: true,
      video: [],
      genre: []
    }
  },
  props: {
    list: {
      requied: true
    }
  },
  mounted () {
    this.fetchmovie(this.$route.params.id)
  },
  
  methods: {
    async fetchmovie (movieid) {
      await api.get("/movie/" + movieid + "?append_to_response=credits,videos,images")
    .then(response => {
      this.movie = response.data
      
      let videos = response.data.videos.results
      this.genre = response.data.genres
      console.log(this.movie)
      const filterPro = videos.filter((product) => {
              return product.name.toLowerCase().includes('official')
            })
            console.log(filterPro[0].key)
            this.video = filterPro[0].key
            // for (let i = 0; i < genre.length; i++) {
            //   const e = genre[i];
            //   console.log(e.name)
            //   this.genre = e.name
            // }
            
    })
    .catch(e => {
      console.log(e)
    })
    
    }
  },
    computed: {
     posterPath() {
       return "https://image.tmdb.org/t/p/w500/" + this.movie.poster_path
     },
     backdrop_path() {
       return "https://image.tmdb.org/t/p/w500/" + this.movie.backdrop_path
     },
     youtubeVideo() {
       if(!this.video) return;
       return ("https://www.youtube.com/embed/" + this.video)
     },

  },

}
</script>

